
@transitiontime: 0.5s;
@cellX: 10.5%;
@cellY: 10.5%;
@cellx: 2%;
@celly: 2%;

@cellX2: 17%;
@cellY2: 17%;

@cellx2: 4%;
@celly2: 4%;
@cellx4: 13%;
@cellx6: 18%;
@celly6: 18%;
@celly4: 13%;

@cellblockx: 4.5%;
@cellblocky: 4.5%;

*, p, p{
  font-family: 'Quicksand';
}

body{
  background: #000000;
  height: 100vh;
}

.navbar-black{
  background: #000000;
  padding-bottom: 20px;
}

.navbar-black a{
  color: #ffffff;
}
.container-fluid{
  height: 100%;
}

.grid-container{
  height: 100%;
}
.red-box{
  background: #FF5755;
}
.blue-box{
  background: #7BBEEA;
}
.green-box{
  background: #7BEAD7;
}
.yellow-box{
  background: #FFDF66;
}
.dark-blue-box{
  background: #2995D8;
}
.dark-yellow-box{
  background: #FFC700;
}
.dark-green-box{
  background: #16C9A8;
}
.dark-red-box{
  background: #F42424;
}
.purple-box{
  background: #998BEC;
}
.black-box{
  opacity: 0.2;
}

.cell-block{
  width: @cellblockx;
  height: @cellblocky;
  border: 1px solid black;
  cursor: pointer;
  text-align: center;
  color: white;
  font-weight: bold;
  font-size: 17px;
  margin: 0;
  border-radius: 2px;
  position: absolute;
  .transition(all @transitiontime ease 0s);
}

.cellxy{
  width: @cellx;
  height: @celly;
  .transition(all @transitiontime ease 0s);
}

.cellXy{
  width: @cellX;
  height: @celly;
  .transition(all @transitiontime ease 0s);
}

.cellxY{
  width: @cellx;
  height: @cellY;
  .transition(all @transitiontime ease 0s);
}
.cellXY {
  width: @cellX;
  height: @cellY;
  .transition(all @transitiontime ease 0s);
}
/*SECOND LEVEL*/
.cellx2y{
  width: @cellx2;
  height: @celly;
  .transition(all @transitiontime ease 0s);
}
.cellxy2{
  width: @cellx;
  height: @celly2;
  .transition(all @transitiontime ease 0s);
}
.cellxy4{
  width: @cellx;
  height: @celly4;
  .transition(all @transitiontime ease 0s);
}
.cellx4y{
  width: @cellx4;
  height: @celly;
  .transition(all @transitiontime ease 0s);
}
.cellx6y{
  width: @cellx6;
  height: @celly;
  .transition(all @transitiontime ease 0s);
}
.cellxy6{
  width: @cellx;
  height: @celly6;
  .transition(all @transitiontime ease 0s);
}
.cellX2Y2{
  width: @cellX2;
  height: @cellY2;
  .transition(all @transitiontime ease 0s);
}
.cell-block .page-container{
  background: none repeat scroll 0 0 rgba(0, 0, 0, 0.5);
  color: white;
  font-size: 30px;
  height: 100%;
  opacity: 0;
  padding-top: 9%;
  display: block;
  .transition(all @transitiontime ease 0s);
}

.cellX2Y2 .page-container{
  opacity: 1;
  .transition(all @transitiontime ease 0s);
}
.showBox{
  width: 89.5%;
  height: 89.50%;
  z-index: 9999;
  opacity: 1;
  .transition(all @transitiontime ease 0s);
}

.showBox .page-container{
  opacity: 1;
  padding-top: 2%;
  .transition(all @transitiontime ease 0s);
}
.page-content{
  height: 90%;
  opacity: 0;
  padding: 0 40px;
  font-size: 17px;
  text-align: justify;
  overflow: auto;
  .transition(all 0s ease 0s);
}
.page-title{
  font-size: 22px;
}

.showBox .page-content{
  opacity: 1;
  overflow: auto;
  .transition(all @transitiontime ease 0.6s);
}